@charset "UTF-8";

.bbs {
	width: 600px;
	margin: 30px auto;
	text-align: left;
}

.bbs_title {
	
	width: 574px;
	padding-left: 20px;
	height: 40px;
	border: 3px solid #FF82C2;
	text-align: left;
	font-weight: bold;
	line-height: 40px;
	font-size: 10pt;
	margin-bottom: 30px;
		
}

.bbsCreated {
	
	width: 600px;
	overflow: visible;
	border-top: 3px solid #DBDBDB;
	border-bottom: 3px solid #DBDBDB;
	text-align: left;
	
}


.bbsCreated_bottomLine {
	
	height: 35px;
	line-height: 35px;
	border-bottom: 1px solid #DBDBDB;
	clear: both;
	text-align: left;
	
}

.bbsCreated_noLine {
	
	height: 35px;
	line-height: 35px;
	clear: both;
	text-align: left;
	
}

.bbsCreated_content {
	
	height: 170px;
	line-height: 170px;
	border-bottom: 1px solid #DBDBDB;
	clear: both;
	text-align: left;
	
}

.bbsCreated dt {
	
	float: left;
	height: 35px;
	width: 80px;
	line-height: 35px;
	text-align: left;
	padding-left: 20px;
	background-color: #EEEEEE;
	
}

.bbsCreated dd {
	
	float: left;
	height: 35px;
	width: 440px;
	line-height: 35px;
	text-align: left;
	padding-left: 10px;
	
}

.bbsCreated_content dt {
	
	float: left;
	height: 170px;
	width: 80px;
	text-align: left;
	padding-left: 20px;
	background-color: #EEEEEE;
	
}

.bbsCreated_content dd {
	
	float: left;
	height: 170px;
	width: 440px;
	line-height: 170px;
	text-align: left;
	padding-left: 10px;
	
}

.bbsCreated_footer {
	
	width: 600px;
	overflow: visible;
	clear: both;
	height: 50px;
	line-height: 50px;
	margin-bottom: 20px;
	text-align: center;
	
}

.boxTF {
	
	border: 1px solid;
	height: 15px;
	margin-top: 7px;
	padding: 2px 2px 2px 2px;
	border-color: #666666;
	background-color: #ffffff;
	font-family: "굴림";
	font-size: 9pt;
	
}

.boxTA {
	
	border: 1px solid;
	height: 150px;
	margin-top: 7px;
	padding: 2px 2px 2px 2px;
	border-color: #666666;
	background-color: #ffffff;
	font-family: "굴림";
	font-size: 9pt;
	
}











