
.bbsList {
	width:690px;
	margin:30px auto;
	text-align:left;
	background-color: #ffffff;
}

.bbsList_title {
	width:664px;   /* 690px - (padding-left+border:3px+border:3px) */
	padding-left:20px;
	height:40px;
	border:3px solid #FF82C2;
	border-radius: 10px;
	text-align:left;
	font-weight: bold;
	line-height:40px;
	font-size:10pt;
	margin-bottom:30px;
}

.bbsList_header {
	height:27px;
}

.bbsList_header .leftHeader{
	float:left;
	width:345px;
	text-align:left;
}
.bbsList_header .rightHeader{
	float:right;
	width:345px;
	text-align:right;
}

.bbsList_header .selectFiled {
	border:1px solid; 
	border-color:#666666; 
	background-color:#ffffff; 
	font-family:굴림; 
	font-size:9pt;
}

.bbsList_header .textFiled {
	border:1px solid; 
	height:13px; 
	padding:2px 2px 2px 2px; 
	border-color:#666666; 
	background-color:#ffffff; 
	font-family:"굴림"; 
	font-size:9pt;
}

.bbsList_list {
	clear:both;
}

.bbsList_list dd, .bbsList_list td {
	float:left;
	height:27px;
	line-height:27px;
	text-align:center;
}

.bbsList_list .title {
	height:30px;
	border-top:1px solid #FF82C2;
	border-bottom:1px solid #FF82C2;
	background-color: #FFDFEF;
}

.bbsList_list #title dl, .bbsList_list .title tr {
	height:27px;
	border-left:0px solid #5db062;
	border-right:0px solid #5db062;
}

.bbsList_list #title dt, .bbsList_list .title th {
	float:left;
	line-height:34px;
	text-align:center;
}

.bbsList_list .num {width:50px;}
.bbsList_list .subject {width:400px;}
.bbsList_list .name {width:90px;}
.bbsList_list .created {width:85px;}
.bbsList_list .hitCount {width:50px;}

.bbsList_list #lists {
	clear:both;
}

.bbsList_list #lists dl, .bbsList_list .lists tr {
	float:left;
	border-bottom:1px solid #FFDFEF;
}

.bbsList_list .lists tr:last-child {
	float: none;
}


.bbsList_list dd.subject, .bbsList_list td.subject {
	width:390px;
	margin-left:10px;
	text-align:left;
}
.bbsList_list a {
	line-height: 27px;
}

.bbsList_list .footer {
	clear:both;
	height:32px;
	line-height:32px;
	margin-top:5px;
	text-align:center;
	float: none;
}
